import React, {useState, useEffect, useCallback, useRef} from 'react';
import 'date-fns';
import {
  makeStyles,
  withStyles,
  Grid,
  Typography,
  TextField,
  FormGroup,
  FormControlLabel,
  FormControl,
  Checkbox,
  Radio,
  RadioGroup,
  Button,
  FormHelperText
} from '@material-ui/core/';

import { teal } from '@material-ui/core/colors';
import moment from 'moment';
import Wrapper from './styles';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import ko from "date-fns/locale/ko"
import { categories } from '../../../config/config';
import { ColorButton } from '../../../common/Buttons'

import { useDispatch, useSelector } from 'react-redux';
import { ADD_CHALLENGE_REQUEST, CLEAR_ADD_CHALLENGE_DONE, CLEAR_IMAGE_PATH, UPLOAD_CHALLENGE_IMAGE_REQUEST } from '../../../reducers/challenge';
import { useHistory } from 'react-router-dom';
import { OPEN_CONFIRM_MODAL } from '../../../reducers/modal';

/* ************************ Main Component Start ************************ */
const CreateChallenge = () => {
  const dispatch = useDispatch()
  const history = useHistory()
  const classes = useStyles();
  const imageInput = useRef()
  const { challengeImagePath, addChallengeDone, addChallengeError } = useSelector((state) => state.challenge)
  const [submitDisable, setSubmitDisable] = useState(true)
  
  //  
  const [name, setName] = useState('')
  const onChangeName = useCallback((e) => {
    setName(e.target.value)
  }, [])
  
  //   =>  
  const [subject, setSubject] = useState('')
  const onChangeSubject = useCallback((e) => {
    setSubject(e.target.value)
  }, [])

  const [certCycle, setCertCycle] = useState('1')
  const onChangeCertCycle = useCallback((e) => {
    setCertCycle(e.target.value)
  }, [])
  
  //   Date
  const [startDate, setStartDate] = useState(moment().startOf('day'));
  
  //   Date
  const [endDate, setEndDate] = useState(startDate);
  
  //     Date
  const [maxDate, setMaxDate] = useState(null)
  
  //   startDate ~ endDate
  const [period, setPeriod] = useState(0)
  
  //     
  useEffect(() => {
    const d = new Date(startDate.valueOf()+(1000*60*60*24*365))
    setMaxDate(d)
  }, [startDate])

  //      
  useEffect(() => {
    dispatch({
      type: CLEAR_IMAGE_PATH,
    })
  }, [])

  //    
  /**
   *  => period
   *   =>  ~     /  Error
   *   =>  ~     /  Error
   *  6 => period*(6/7)
   *  5 => period*(5/7)
   *  4 => period*(4/7)
   *  3 => period*(3/7)
   *  2 => period*(2/7)
   *  1 => period*(1/7)
   */
  const [totalNumOfCert, setTotalNumOfCert] = useState(0)
  const [totalCertError, setTotalCertError] = useState(false)

  //     
  const getWeekdayNum = () => {
    const tempDate = new Date(startDate.getTime())
    let count = 0
    while (true) {
      if (tempDate.getTime() >= endDate.getTime()) {
        return count
      } else {
        let temp = tempDate.getDay()
        if (temp !== 0 && temp !== 6) {
          count++
        }
      }
      tempDate.setDate(tempDate.getDate()+1)
    }
  }

  //     
  const getWeekendNum = () => {
    const tempDate = new Date(startDate.getTime())
    let count = 0
    while (true) {
      if (tempDate.getTime() >= endDate.getTime()) {
        return count
      } else {
        let temp = tempDate.getDay()
        if (temp === 0 || temp === 6) {
          count++
        }
      }
      tempDate.setDate(tempDate.getDate()+1)
    }
  }

  //    
  const [selectWeek, setSelectWeek] = useState(0)
  const onChangeSelectWeek = useCallback((e) => {
    if (e.target.value === '5') {
      setSelectWeek(e.target.value)
      setWeek(e.target.value*1)
    } else {
      setSelectWeek(e.target.value)
      setWeek(e.target.value*1)
    }
  }, [])

  const [week, setWeek] = useState(0)
  const [weekError, setWeekError] = useState(false)
  const onChangeWeek = useCallback((e) => {
    if (isNaN(e.target.value)) {
      setWeekError(true)
    } else {
      setWeek(e.target.value)
      if (e.target.value > 52 || e.target.value < 5) {
        setWeekError(true)
      } else {
        setWeekError(false)
      }
    }
  }, [])

  //    
  const [activeWeekDay, setActiveWeekDay] = useState({
    mon: true,
    tue: true,
    wed: true,
    thu: true,
    fri: true,
    sat: true,
    sun: true
  })
  const onChangeActiveWeekDay = (e) => {
    setActiveWeekDay({ ...activeWeekDay, [e.target.name]: e.target.checked })
  }
  const { mon, tue, wed, thu, fri, sat, sun } = activeWeekDay
  const activeWeekError = certCycle <= 3 ? false : [mon, tue, wed, thu, fri, sat, sun].filter((v) => v).length < (10 - certCycle)

  //      ...
  useEffect(() => {
    if (certCycle*1 <= 3) {
      setPeriod(Math.ceil((endDate - startDate) / (1000*60*60*24)))
    } else {
      setPeriod(week*7)
    }
  }, [certCycle, week, startDate, endDate])

  useEffect(() => {
    if (certCycle*1 <= 3) {
      if (certCycle*1 === 1) {
        setTotalCertError(false)
        setTotalNumOfCert(period)
        setActiveWeekDay({
          mon: true,
          tue: true,
          wed: true,
          thu: true,
          fri: true,
          sat: true,
          sun: true
        })
      } else if (certCycle*1 === 2) {
        const result = getWeekdayNum()
        if (result === 0) setTotalCertError(true)
        else setTotalCertError(false)
        setTotalNumOfCert(result)
        setActiveWeekDay({
          mon: true,
          tue: true,
          wed: true,
          thu: true,
          fri: true,
          sat: false,
          sun: false,
        })
      } else if (certCycle*1 === 3) {
        const result = getWeekendNum()
        if (result === 0) setTotalCertError(true)
        else setTotalCertError(false)
        setTotalNumOfCert(result)
        setActiveWeekDay({
          mon: false,
          tue: false,
          wed: false,
          thu: false,
          fri: false,
          sat: true,
          sun: true,
        })
      }
    } else {
      const tempEnd = new Date(startDate.valueOf() + period*1000*60*60*24)
      setEndDate(tempEnd)
      setTotalNumOfCert(period*(10-certCycle)/7)
    }
  }, [period, certCycle, startDate])

  //     ...
  // useEffect(() => {
  //   console.log(totalNumOfCert)
  // }, [totalNumOfCert])

  //      (24,  )
  const [certTimeMode, setCertTimeMode] = useState('1')
  const onChangeCertTimeMode = useCallback((e) => {
    setCertTimeMode(e.target.value)
  }, [])

  //     
  const [certStartTime, setCertStartTime] = useState('00:00')
  const onChangeCertStartTime = useCallback((e) => {
    setCertStartTime(e.target.value)
  }, [])

  //     
  const [certEndTime, setCertEndTime] = useState('00:00')
  const onChangeCertEndTime = useCallback((e) => {
    setCertEndTime(e.target.value)
  }, [])

  const certTimeError = certTimeMode === '1' ? false : certStartTime > certEndTime

  useEffect(() => {
    if (certTimeMode === '1') {
      setCertStartTime('00:00')
      setCertEndTime('00:00')
    }
  }, [certTimeMode])

  //   
  const [content, setIntroduce] = useState('')
  const onChangeIntroduce = useCallback((e) => {
    setIntroduce(e.target.value)
  }, [])

  useEffect(() => {
    if (name === '' || subject === '' || challengeImagePath === '' || content === '' || startDate === '' || endDate === '' || period <= 0 || totalNumOfCert <= 0 || certStartTime === '' || certEndTime === '' || activeWeekError || weekError || certTimeError || totalCertError) {
      setSubmitDisable(true)
    }
    if (name !== '' && subject !== '' && challengeImagePath !== '' && content !== '' && startDate !== '' && endDate !== '' && period !== 0 && totalNumOfCert !== 0 && certStartTime !== '' && certEndTime !== '' && !activeWeekError && !weekError && !certTimeError && !totalCertError) {
      setSubmitDisable(false)
    }
  }, [name, subject, challengeImagePath, content, startDate, endDate, period, totalNumOfCert, certStartTime, certEndTime])
  
  const onClickImageUpload = useCallback(() => {
    imageInput.current.click()
  }, [imageInput.current])

  const onChangeImage = useCallback((e) => {
    console.log('image', e.target.files[0])

    const imageFormData = new FormData()
    imageFormData.append('image', e.target.files[0])

    dispatch({
      type: UPLOAD_CHALLENGE_IMAGE_REQUEST,
      data: imageFormData
    })
  }, [])
  
  const onSubmit = useCallback((e) => {
    e.preventDefault()

    dispatch({
      type: ADD_CHALLENGE_REQUEST,
      data:{
        name: name,
        img_addr: challengeImagePath,
        content: content,
        start_date: startDate,
        end_date: endDate,
        period: period,
        certification_cycle: certCycle,
        total_number_of_certification: totalNumOfCert,
        cert_available: {
          cert_available_start_time: certStartTime,
          cert_available_end_time: certEndTime
        },
        cert_day: [
          {data: mon},
          {data: tue},
          {data: wed},
          {data: thu},
          {data: fri},
          {data: sat},
          {data: sun}
        ],
        CategoryId: subject*1
      }
    })
  }, [name, subject, challengeImagePath, content, startDate, endDate, period, certCycle, totalNumOfCert, certStartTime, certEndTime,
    mon, tue, wed, thu, fri, sat, sun, categories]);

  useEffect(() => {
    if (addChallengeDone) {
      dispatch({
        type: OPEN_CONFIRM_MODAL,
        message: ' !'
      })
      dispatch({
        type: CLEAR_ADD_CHALLENGE_DONE
      })
      history.push('/Home')
    }
    if (addChallengeError) {
      dispatch({
        type: OPEN_CONFIRM_MODAL,
        message: addChallengeError
      })
    }
  }, [addChallengeDone, addChallengeError])

  const onCancel = useCallback(() => {
    history.push('/ChallengeHome')
  }, [])

  return (
    <Wrapper>
      <form encType="multipart/form-data" onSubmit={onSubmit}>
        <Grid container spacing={3}>
          <Grid item xs={12} className="titleGrid">
            <h1> </h1>
          </Grid>
          {/* ************************   ************************* */}
          <Grid item xs={10}>
            <h3>1.    !</h3>
            <TealColor
              fullWidth
              id="standard-full-width"
              InputLabelProps={{
                shrink: true,
              }}
              margin="normal"
              onChange={onChangeName}
              placeholder="ex. 1 1"
              value={name}
            />
          </Grid>
          {/* ************************   ************************* */}
          <Grid item xs={12}>
            <h3>2.    ?</h3>
            <FormControl component="fieldset" style={{ margin: '10px'}}>
              <RadioGroup name="" value={subject} onChange={onChangeSubject}>
                {
                  categories.map((e, i)=>{
                    return  <FormControlLabel key={i} value={e.name} control={<TealRadio />} label={e.label} />
                  })
                }
              </RadioGroup>
            </FormControl>
          </Grid>
          {/* ************************   ************************* */}
          <Grid item xs={12}>
            <h3>3.   ?</h3>
            <FormControl component="fieldset" style={{ margin: '10px'}}>
              <RadioGroup name="" value={certCycle} onChange={onChangeCertCycle}>
                <FormControlLabel value="1" control={<TealRadio />} label="" />
                <FormControlLabel value="2" control={<TealRadio />} label=" " />
                <FormControlLabel value="3" control={<TealRadio />} label=" " />
                <FormControlLabel value="4" control={<TealRadio />} label=" 6" />
                <FormControlLabel value="5" control={<TealRadio />} label=" 5" />
                <FormControlLabel value="6" control={<TealRadio />} label=" 4" />
                <FormControlLabel value="7" control={<TealRadio />} label=" 3" />
                <FormControlLabel value="8" control={<TealRadio />} label=" 2" />
                <FormControlLabel value="9" control={<TealRadio />} label=" 1" />
              </RadioGroup>
            </FormControl>
          </Grid>
          {/* ************************   ************************* */}        
          <Grid item xs={12}>
            <h3>4.  ?</h3>
          </Grid>
          {certCycle <= 3
            ? (
            <>
              {/*     !    ,    ! */}
              <Grid item xs={12} sm={6} md={3}>
                <h3 className="dateTitle"></h3>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    disableToolbar
                    format="yyyy/MM/dd"
                    invalidDateMessage="   !"
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                    locale={ko}
                    margin="normal"
                    minDate={Date.now()}
                    onChange={date => setStartDate(date)}
                    selected={startDate}
                    value={startDate}
                    variant="inline"
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <h3 className="dateTitle"></h3>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    disableToolbar
                    format="yyyy/MM/dd"
                    invalidDateMessage="   !"
                    KeyboardButtonProps={{
                      'aria-label': 'change date',
                    }}
                    locale={ko}
                    margin="normal"
                    maxDate={maxDate}
                    maxDateMessage="  1   ."
                    minDate={startDate}
                    minDateMessage="      ."
                    onChange={date => setEndDate(date)}
                    selected={startDate}
                    value={endDate}
                    variant="inline"
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={12} sm={4} md={3}>
                <h2 >  {period ? period : 0} </h2>
                <h2>  {totalNumOfCert ? totalNumOfCert : 0} </h2>
                {
                  totalCertError ? <FormHelperText error={totalCertError}>   0  !</FormHelperText> : null
                }
              </Grid>
            </>
            )
            : (
            <>
              <Grid item xs={12} sm={6} md={3}>
                <h3 className="dateTitle"></h3>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    disableToolbar
                    variant="inline"
                    format="yyyy/MM/dd"
                    margin="normal"
                    value={startDate}
                    onChange={date => setStartDate(date)}
                    selected={startDate}
                    minDate={Date.now()}
                    locale={ko}
                    KeyboardButtonProps={{
                        'aria-label': 'change date',
                    }}
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <h3 className="dateTitle"></h3>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                  <KeyboardDatePicker
                    disableToolbar
                    variant="inline"
                    format="yyyy/MM/dd"
                    margin="normal"
                    value={endDate}
                    maxDate={maxDate}
                    maxDateMessage="  1   ."
                    locale={ko}
                    disabled
                  />
                </MuiPickersUtilsProvider>
              </Grid>
              <Grid item xs={12} sm={6} md={3}>
                <Typography variant='h5' >  {period ? period : 0} </Typography>
                <Typography variant='h5' >  {totalNumOfCert ? totalNumOfCert : 0} </Typography>
                {
                  totalCertError ? <FormHelperText error={totalCertError}>   0  !</FormHelperText> : null
                }
              </Grid>
              <Grid item xs={12}>
                <FormControl component="fieldset" style={{ margin: '10px'}}>
                  <RadioGroup name="" value={selectWeek} onChange={onChangeSelectWeek}>
                    <FormControlLabel value="1" control={<TealRadio />} label="1" />
                    <FormControlLabel value="2" control={<TealRadio />} label="2" />
                    <FormControlLabel value="3" control={<TealRadio />} label="3" />
                    <FormControlLabel value="4" control={<TealRadio />} label="4" />
                    <FormControlLabel value="5" control={<TealRadio />} label="" />
                    {
                      selectWeek === '5'
                        ? 
                        <>
                          <TealColor
                            InputProps={{
                              inputProps: { min: 5, max: 52 }
                            }}
                            InputLabelProps={{
                              shrink: true,
                            }}
                            margin="normal"
                            onChange={onChangeWeek}
                            type="number"
                            value={week}
                          />
                          <FormHelperText error={weekError} >
                            5~52    !
                          </FormHelperText>
                        </>
                        : null
                    }
                  </RadioGroup>
                </FormControl>
              </Grid>
            </>
            )
          }
          {/* ************************     ************************* */}
          {
            certCycle >= 4
              ?
              <Grid item xs={12}>
                <h3>+    ?</h3>
                <FormControl error={activeWeekError} component="fieldset" style={{ margin: '10px'}}>
                  <FormGroup>
                    <FormControlLabel control={<GreenCheckbox checked={mon} onChange={onChangeActiveWeekDay} name="mon"/>} label="" />
                    <FormControlLabel control={<GreenCheckbox checked={tue} onChange={onChangeActiveWeekDay} name="tue"/>} label="" />
                    <FormControlLabel control={<GreenCheckbox checked={wed} onChange={onChangeActiveWeekDay} name="wed"/>} label="" />
                    <FormControlLabel control={<GreenCheckbox checked={thu} onChange={onChangeActiveWeekDay} name="thu"/>} label="" />
                    <FormControlLabel control={<GreenCheckbox checked={fri} onChange={onChangeActiveWeekDay} name="fri"/>} label="" />
                    <FormControlLabel control={<GreenCheckbox checked={sat} onChange={onChangeActiveWeekDay} name="sat"/>} label="" />
                    <FormControlLabel control={<GreenCheckbox checked={sun} onChange={onChangeActiveWeekDay} name="sun"/>} label="" />
                    {
                      activeWeekError
                        ? <FormHelperText>          !</FormHelperText>
                        : null
                    }
                  </FormGroup>
                </FormControl>
              </Grid>
              : null
          }
          {/* ************************     ************************* */}
          <Grid item xs={12}>
            <h3>5.   ?</h3>
            <FormControl className={classes.formControl} style={{ margin: '10px'}}>
              <RadioGroup value={certTimeMode} onChange={onChangeCertTimeMode}>
                <FormControlLabel value="1" control={<TealRadio />} label="24" />
                <FormControlLabel value="2" control={<TealRadio />} label=" " />
              </RadioGroup>
            </FormControl>
          </Grid>
          {
            certTimeMode === '2'
              ?
              <>
                <Grid item xs={12} sm={6} md={3} >
                  <TextField
                    className={classes.textField}
                    id="time"
                    inputProps={{
                      step: 300, // 5 min
                    }}
                    InputLabelProps={{
                      shrink: true,
                    }}
                    label=""
                    onChange={onChangeCertStartTime}
                    style={{ padding: '10px 0' }}
                    type="time"
                    value={certStartTime}
                  />
                </Grid>
                <Grid item xs={12} sm={6} md={3} >
                  <TextField
                    className={classes.textField}
                    id="time"
                    inputProps={{
                      step: 300, // 5 min
                    }}
                    InputLabelProps={{
                      shrink: true,
                    }}
                    label=""
                    onChange={onChangeCertEndTime}
                    style={{ padding: '10px 0' }}
                    type="time"
                    value={certEndTime}
                  />
                  <FormHelperText error={certTimeError}>     !</FormHelperText>
                </Grid>
              </>
              : null
          }
          {/* ************************   ************************* */}
          <Grid item xs={12} >
            <h3>6.   !</h3>
          </Grid>
          <Grid item xs={12} sm={3} >
            <ColorButton fullWidth variant="contained" onClick={onClickImageUpload}>   </ColorButton>
          </Grid>
          <Grid item xs={12} sm={3} >
            <div style={{ display: 'flex', justifyContent: 'center' }}>
              <img accept="image/*" src={challengeImagePath ? challengeImagePath : "/images/camera.png"} style={{ maxHeight: '150px', maxWidth: '150px' }} />
            </div>
            <input type="file" name="image" hidden ref={imageInput} onChange={onChangeImage} />
          </Grid>
          <Grid item xs={12} sm={6} >
            <TextField
              id="outlined-multiline-static"
              multiline
              fullWidth
              value={content}
              rows={4}
              variant="outlined"
              onChange={onChangeIntroduce}
            />
          </Grid>
          {/* ************************   !!! ************************* */}
          <Grid item xs={12} style={{ margin: '20px 0'}}>
            <ColorButton variant="contained" type="submit" disabled={submitDisable} >  !</ColorButton>
            <Button variant="contained" onClick={onCancel} style={{ margin: '0 10px' }} ></Button>
          </Grid>
        </Grid>
      </form>
    </Wrapper>
  );
}

/* ************************ Style Section Start ************************ */

const GreenCheckbox = withStyles({
  root: {
    color: teal[400],
    '&$checked': {
      color: teal[600],
    },
  },
  checked: {},
})((props) => <Checkbox color="default" {...props} />);

const TealRadio = withStyles({
  root: {
    color: teal[400],
    '&$checked': {
      color: teal[600],
    },
  },
  checked: {},
})((props) => <Radio color="default" {...props} />);

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: '25ch',
  },
}));

const TealColor = withStyles((theme) => ({
  root: {
    '& label.Mui-focused': {
      color: 'teal',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: 'teal',
    },     
  },    
}))(TextField)

export default CreateChallenge